<template>
  <div class="m-block">
    <div class="m-block--tit">銷毀5億枚以下(比例分母為1000)</div>
    <div class="f-block--tit">買入扣除手續費進入營銷錢包(當前值：{{buyRate.buyRate}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setBuyRateValue" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setBuyRate"
        >確認<br>(setBuyRate)</van-button>
      </div>
    </div>
    <div class="f-block--tit">買入手續費進保險基金池比例(當前值：{{buyFundRate.buyFundRate}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setbuyFundRateValue" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setbuyFundRate"
        >確認<br>(setbuyFundRate)</van-button>
      </div>
    </div>
    <div class="f-block--tit">轉賬扣除手續費進入營銷錢包(當前值：{{transferRate.transferRate}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setTransferRateValue" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setTransferRate"
        >確認<br>(setTransferRate)</van-button>
      </div>
    </div>
    <div class="f-block--tit">轉賬手續費進保險基金池比例(當前值：{{transferFundRate.transferFundRate}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="settransferFundRateValue" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="settransferFundRate"
        >確認<br>(settransferFundRate)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣分配給保險基金池比例(當前值：{{fundRate.fundRate}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setFundRateValue" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setFundRate"
        >確認<br>(setFundRate)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣添加底池比例(當前值：{{liquidityRate.liquidityRate}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setLiquidityRateValue" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setLiquidityRate"
        >確認<br>(setLiquidityRate)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣技術開發比例(當前值：{{teRate.teRate}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setTeRateValue" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setTeRate"
        >確認<br>(setTeRate)</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
export default {
  props: [
    'buyRate', 
    'buyFundRate', 
    'transferRate', 
    'transferFundRate', 
    'fundRate', 
    'liquidityRate', 
    'teRate'
  ],
  emits: ['setBuyRate', 'setbuyFundRate', 'setTransferRate', 'settransferFundRate', 'setFundRate', 'setLiquidityRate', 'setTeRate'],
  setup (props, {emit}) {
    const data = reactive({
      setBuyRateValue: '',
      setbuyFundRateValue: "",
      setTransferRateValue: '',
      settransferFundRateValue: '',
      setFundRateValue: '',
      setLiquidityRateValue: '',
      setTeRateValue: '',
      setBuyRate: () => {
        emit('setBuyRate', data.setBuyRateValue)
      },
      setbuyFundRate: () => {
        emit('setbuyFundRate', data.setbuyFundRateValue)
      },
      setTransferRate: () => {
        emit('setTransferRate', data.setTransferRateValue)
      },
      settransferFundRate: () => {
        emit('settransferFundRate', data.settransferFundRateValue)
      },
      setFundRate: () => {
        emit('setFundRate', data.setFundRateValue)
      },
      setLiquidityRate: () => {
        emit('setLiquidityRate', data.setLiquidityRateValue)
      },
      setTeRate: () => {
        emit('setTeRate', data.setTeRateValue)
      },
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.m-block {
  background: #fff;
  margin: 15px;
  padding: 15px;
  .m-block--tit {
    line-height: 32px;
    margin: 0 0 15px;
  }
  .f-block--tit {
    font-size: 12px;
    color: #646566;
  }
  .f-block {
    margin: 8px 0 15px;
  }
  .cells-box {
    .van-cell {
      padding: 10px 16px;
      background: #eef0f3;
    }
  }
  .button-wrapper {
    margin: 0 0 0 15px;
    min-width: 120px;
  }
}
</style>