    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setBuyRate7Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setBuyRate7"
        >確認<br>(setBuyRate7)</van-button>
      </div>
    </div>
    <div class="f-block--tit">買入手續費進保險基金池比例(當前值：{{buyFundRate.buyFundRate7}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setbuyFundRate7Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setbuyFundRate7"
        >確認<br>(setbuyFundRate7)</van-button>
      </div>
    </div>
    <div class="f-block--tit">轉賬扣除手續費進入營銷錢包(當前值：{{transferRate.transferRate7}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setTransferRate7Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setTransferRate7"
        >確認<br>(setTransferRate7)</van-button>
      </div>
    </div>
    <div class="f-block--tit">轉賬手續費進保險基金池比例(當前值：{{transferFundRate.transferFundRate7}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="settransferFundRate7Value" label="比例" placeholder="uint256" />
      </van-cell-group>
