      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setFundRate7Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setFundRate7"
        >確認<br>(setFundRate7)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣添加底池比例(當前值：{{liquidityRate.liquidityRate7}})</div>
