      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setLiquidityRate7Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setLiquidityRate7"
        >確認<br>(setLiquidityRate7)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣技術開發比例(當前值：{{teRate.teRate7}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setTeRate7Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setTeRate7"
        >確認<br>(setTeRate7)</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
export default {
  props: [
    'buyRate', 
    'buyFundRate', 
    'transferRate', 
    'transferFundRate', 
    'fundRate', 
    'liquidityRate', 
    'teRate'
  ],
  emits: ['setBuyRate7', 'setbuyFundRate7', 'setTransferRate7', 'settransferFundRate7', 'setFundRate7', 'setLiquidityRate7', 'setTeRate7'],
  setup (props, {emit}) {
    const data = reactive({
      setBuyRate7Value: '',
      setbuyFundRate7Value: "",
      setTransferRate7Value: '',
      settransferFundRate7Value: '',
      setFundRate7Value: '',
      setLiquidityRate7Value: '',
      setTeRate7Value: '',
      setBuyRate7: () => {
        emit('setBuyRate7', data.setBuyRate7Value)
      },
      setbuyFundRate7: () => {
        emit('setbuyFundRate7', data.setbuyFundRate7Value)
      },
      setTransferRate7: () => {
        emit('setTransferRate7', data.setTransferRate7Value)
      },
      settransferFundRate7: () => {
        emit('settransferFundRate7', data.settransferFundRate7Value)
      },
      setFundRate7: () => {
        emit('setFundRate7', data.setFundRate7Value)
      },
      setLiquidityRate7: () => {
        emit('setLiquidityRate7', data.setLiquidityRate7Value)
      },
      setTeRate7: () => {
        emit('setTeRate7', data.setTeRate7Value)
      },
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
