    <div class="f-block--tit">買入扣除手續費進入營銷錢包(當前值：{{buyRate.buyRate95}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setBuyRate95Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setBuyRate95"
        >確認<br>(setBuyRate95)</van-button>
      </div>
    </div>
    <div class="f-block--tit">買入手續費進保險基金池比例(當前值：{{buyFundRate.buyFundRate95}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setbuyFundRate95Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setbuyFundRate95"
        >確認<br>(setbuyFundRate95)</van-button>
      </div>
    </div>
    <div class="f-block--tit">轉賬扣除手續費進入營銷錢包(當前值：{{transferRate.transferRate95}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setTransferRate95Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setTransferRate95"
        >確認<br>(setTransferRate95)</van-button>
      </div>
    </div>
    <div class="f-block--tit">轉賬手續費進保險基金池比例(當前值：{{transferFundRate.transferFundRate95}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="settransferFundRate95Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="settransferFundRate95"
        >確認<br>(settransferFundRate95)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣分配給保險基金池比例(當前值：{{fundRate.fundRate95}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setFundRate95Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setFundRate95"
        >確認<br>(setFundRate95)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣添加底池比例(當前值：{{liquidityRate.liquidityRate95}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setLiquidityRate95Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setLiquidityRate95"
        >確認<br>(setLiquidityRate95)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣技術開發比例(當前值：{{teRate.teRate95}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setTeRate95Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setTeRate95"
        >確認<br>(setTeRate95)</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
export default {
  props: [
    'buyRate', 
    'buyFundRate', 
    'transferRate', 
    'transferFundRate', 
    'fundRate', 
    'liquidityRate', 
    'teRate'
  ],
  emits: ['setBuyRate95', 'setbuyFundRate95', 'setTransferRate95', 'settransferFundRate95', 'setFundRate95', 'setLiquidityRate95', 'setTeRate95'],
  setup (props, {emit}) {
    const data = reactive({
      setBuyRate95Value: '',
      setbuyFundRate95Value: "",
      setTransferRate95Value: '',
      settransferFundRate95Value: '',
      setFundRate95Value: '',
      setLiquidityRate95Value: '',
      setTeRate95Value: '',
      setBuyRate95: () => {
        emit('setBuyRate95', data.setBuyRate95Value)
      },
      setbuyFundRate95: () => {
        emit('setbuyFundRate95', data.setbuyFundRate95Value)
      },
      setTransferRate95: () => {
        emit('setTransferRate95', data.setTransferRate95Value)
      },
      settransferFundRate95: () => {
        emit('settransferFundRate95', data.settransferFundRate95Value)
      },
      setFundRate95: () => {
        emit('setFundRate95', data.setFundRate95Value)
      },
      setLiquidityRate95: () => {
        emit('setLiquidityRate95', data.setLiquidityRate95Value)
      },
      setTeRate95: () => {
        emit('setTeRate95', data.setTeRate95Value)
      },
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
