<template>
  <div class="m-block">
    <div class="m-block--tit">每日產出量(當前值：{{rewardPerTime}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setRewardPerTimeValue" label="產出量" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setRewardPerTime"
        >確認<br>(setRewardPerTime)</van-button>
      </div>
    </div>
  </div>
  <div class="m-block">
    <div class="m-block--tit">每日產出銷毀量(當前值：{{destroyPerTime}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setdestroyPerTimeValue" label="產出銷毀量" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setdestroyPerTime"
        >確認<br>(setdestroyPerTime)</van-button>
      </div>
    </div>
  </div>
  <div class="m-block">
    <div class="m-block--tit">解押銷毀比例(比例分母為100,當前值：{{burnLpRate}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setburnLpRateValue" label="銷毀比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setburnLpRate"
        >確認<br>(setburnLpRate)</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
export default {
  props: ['rewardPerTime', 'destroyPerTime', 'burnLpRate'],
  emits: ['setRewardPerTime', 'setdestroyPerTime', 'setburnLpRate'],
  setup (props, {emit}) {
    const data = reactive({
      setRewardPerTimeValue: '',
      setdestroyPerTimeValue: '',
      setburnLpRateValue: '',
      setRewardPerTime: () => {
        emit('setRewardPerTime', data.setRewardPerTimeValue)
      },
      setdestroyPerTime: () => {
        emit('setdestroyPerTime', data.setdestroyPerTimeValue)
      },
      setburnLpRate: () => {
        emit('setburnLpRate', data.setburnLpRateValue)
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.m-block {
  background: #fff;
  margin: 15px;
  padding: 15px;
  .m-block--tit {
    line-height: 32px;
    margin: 0 0 15px;
  }
  .f-block {
    margin: 15px 0;
  }
  .cells-box {
    .van-cell {
      padding: 10px 16px;
      background: #eef0f3;
    }
  }
  .button-wrapper {
    margin: 0 0 0 15px;
    min-width: 120px;
  }
}
</style>