<template>
  <div class="m-block">
    <div class="m-block--tit">區間</div>
    <div class="f-block--tit">第一區間5億(當前值：{{amount.amount5}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setamount5Value" label="區間" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setamount5"
        >確認<br>(setamount5)</van-button>
      </div>
    </div>
    <div class="f-block--tit">第二區間7億(當前值：{{amount.amount7}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setamount7Value" label="區間" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setamount7"
        >確認<br>(setamount7)</van-button>
      </div>
    </div>
    <div class="f-block--tit">第三區間9.5億(當前值：{{amount.amount95}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setamount95Value" label="區間" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setamount95"
        >確認<br>(setamount95)</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
export default {
  props: ['amount'],
  emits: ['setamount5', 'setamount7', 'setamount95'],
  setup (props, {emit}) {
    const data = reactive({
      setamount5Value: '',
      setamount7Value: "",
      setamount95Value: '',
      setamount5: () => {
        emit('setamount5', data.setamount5Value)
      },
      setamount7: () => {
        emit('setamount7', data.setamount7Value)
      },
      setamount95: () => {
        emit('setamount95', data.setamount95Value)
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.m-block {
  background: #fff;
  margin: 15px;
  padding: 15px;
  .m-block--tit {
    line-height: 32px;
    margin: 0 0 15px;
  }
  .f-block--tit {
    font-size: 12px;
    color: #646566;
  }
  .f-block {
    margin: 8px 0 15px;
  }
  .cells-box {
    .van-cell {
      padding: 10px 16px;
      background: #eef0f3;
    }
  }
  .button-wrapper {
    margin: 0 0 0 15px;
    min-width: 120px;
  }
}
</style>