<template>
  <div class="m-block">
    <div class="m-block--tit">提幣</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field v-model="addressValue" label="地址" placeholder="address" />
        <van-field type="number" v-model="amountValue" label="數量" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="withdraw"
        >確認<br>(withdraw)</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
export default {
  props: [],
  emits: ['withdraw'],
  setup (props, {emit}) {
    const data = reactive({
      addressValue: '',
      amountValue: '',
      withdraw: () => {
        emit('withdraw', data.addressValue, data.amountValue)
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.m-block {
  background: #fff;
  margin: 15px;
  padding: 15px;
  .m-block--tit {
    line-height: 32px;
    margin: 0 0 15px;
  }
  .f-block--tit {
    font-size: 12px;
    color: #646566;
  }
  .f-block {
    margin: 8px 0 15px;
    align-items: flex-end;
  }
  .cells-box {
    .van-cell {
      padding: 10px 16px;
      background: #eef0f3;
    }
  }
  .button-wrapper {
    margin: 0 0 0 15px;
    min-width: 120px;
  }
}
</style>