    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setBuyRate5Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setBuyRate5"
        >確認<br>(setBuyRate5)</van-button>
      </div>
    </div>
    <div class="f-block--tit">買入手續費進保險基金池比例(當前值：{{buyFundRate.buyFundRate5}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setbuyFundRate5Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setbuyFundRate5"
        >確認<br>(setbuyFundRate5)</van-button>
      </div>
    </div>
    <div class="f-block--tit">轉賬扣除手續費進入營銷錢包(當前值：{{transferRate.transferRate5}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setTransferRate5Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setTransferRate5"
        >確認<br>(setTransferRate5)</van-button>
      </div>
    </div>
    <div class="f-block--tit">轉賬手續費進保險基金池比例(當前值：{{transferFundRate.transferFundRate5}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="settransferFundRate5Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="settransferFundRate5"
        >確認<br>(settransferFundRate5)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣分配給保險基金池比例(當前值：{{fundRate.fundRate5}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setFundRate5Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setFundRate5"
        >確認<br>(setFundRate5)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣添加底池比例(當前值：{{liquidityRate.liquidityRate5}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setLiquidityRate5Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setLiquidityRate5"
        >確認<br>(setLiquidityRate5)</van-button>
      </div>
    </div>
    <div class="f-block--tit">賣出的幣技術開發比例(當前值：{{teRate.teRate5}})</div>
    <div class="f-block flex">
      <van-cell-group class="cells-box">
        <van-field type="number" v-model="setTeRate5Value" label="比例" placeholder="uint256" />
      </van-cell-group>
      <div class="button-wrapper">
        <van-button 
          type="primary" 
          block 
          @click="setTeRate5"
        >確認<br>(setTeRate5)</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
export default {
  props: [
    'buyRate', 
    'buyFundRate', 
    'transferRate', 
    'transferFundRate', 
    'fundRate', 
    'liquidityRate', 
    'teRate'
  ],
  emits: ['setBuyRate5', 'setbuyFundRate5', 'setTransferRate5', 'settransferFundRate5', 'setFundRate5', 'setLiquidityRate5', 'setTeRate5'],
  setup (props, {emit}) {
    const data = reactive({
      setBuyRate5Value: '',
      setbuyFundRate5Value: "",
      setTransferRate5Value: '',
      settransferFundRate5Value: '',
      setFundRate5Value: '',
      setLiquidityRate5Value: '',
      setTeRate5Value: '',
      setBuyRate5: () => {
        emit('setBuyRate5', data.setBuyRate5Value)
      },
      setbuyFundRate5: () => {
        emit('setbuyFundRate5', data.setbuyFundRate5Value)
      },
      setTransferRate5: () => {
        emit('setTransferRate5', data.setTransferRate5Value)
      },
      settransferFundRate5: () => {
        emit('settransferFundRate5', data.settransferFundRate5Value)
      },
      setFundRate5: () => {
        emit('setFundRate5', data.setFundRate5Value)
      },
      setLiquidityRate5: () => {
        emit('setLiquidityRate5', data.setLiquidityRate5Value)
      },
      setTeRate5: () => {
        emit('setTeRate5', data.setTeRate5Value)
      },
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
