  }
  .f-block--tit {
    font-size: 12px;
    color: #646566;
  }
  .f-block {
    margin: 8px 0 15px;
  }
  .cells-box {
    .van-cell {
      padding: 10px 16px;
      background: #eef0f3;
    }
  }
  .button-wrapper {
    margin: 0 0 0 15px;
    min-width: 120px;
  }
}
</style>