        data.dsSwapContract = new data.web3.eth.Contract(dsSwap, config.dsSwap)
        data.getData()
      },
      getData: async () => {
        let rewardPerTime = await data.dslpstakeContract.methods.rewardPerTime().call()
        data.rewardPerTime = rewardPerTime
        let destroyPerTime = await data.dslpstakeContract.methods.destroyPerTime().call()
        data.destroyPerTime = destroyPerTime
        let burnLpRate = await data.dslpstakeContract.methods.burnLpRate().call()
        data.burnLpRate = burnLpRate

        let buyRate =  await data.dsCoinContract.methods.buyRate().call()
        data.buyRate.buyRate = buyRate
        let buyRate5 =  await data.dsCoinContract.methods.buyRate5().call()
        data.buyRate.buyRate5 = buyRate5
        let buyRate7 =  await data.dsCoinContract.methods.buyRate7().call()
        data.buyRate.buyRate7 = buyRate7
        let buyRate95 =  await data.dsCoinContract.methods.buyRate95().call()
        data.buyRate.buyRate95 = buyRate95
        console.log('buyRate', data.buyRate)

