        data.buyFundRate.buyFundRate = buyFundRate
        let buyFundRate5 =  await data.dsCoinContract.methods.buyFundRate5().call()
        data.buyFundRate.buyFundRate5 = buyFundRate5
        let buyFundRate7 =  await data.dsCoinContract.methods.buyFundRate7().call()
        data.buyFundRate.buyFundRate7 = buyFundRate7
