        data.liquidityRate.liquidityRate5 = liquidityRate5
        let liquidityRate7 =  await data.dsCoinContract.methods.liquidityRate7().call()
        data.liquidityRate.liquidityRate7 = liquidityRate7
        let liquidityRate95 =  await data.dsCoinContract.methods.liquidityRate95().call()
        data.liquidityRate.liquidityRate95 = liquidityRate95
        console.log('liquidityRate', data.liquidityRate)

