        data.teRate.teRate7 = teRate7
        let teRate95 =  await data.dsCoinContract.methods.teRate95().call()
        data.teRate.teRate95 = teRate95
        console.log('teRate', data.teRate)

        let amount5 =  await data.dsCoinContract.methods.amount5().call()
        data.amount.amount5 = amount5
        let amount7 =  await data.dsCoinContract.methods.amount7().call()
        data.amount.amount7 = amount7
        let amount95 =  await data.dsCoinContract.methods.amount95().call()
        data.amount.amount95 = amount95
        console.log('amount', data.amount)
      },
      setBuyRate: async (value) => {
        console.log('setBuyRate', value)
        await data.dsCoinContract.methods.setBuyRate(value).send({from:data.address})
        data.getData()
      },
      setBuyRate5: async (value) => {
        console.log('setBuyRate5', value)
        await data.dsCoinContract.methods.setBuyRate5(value).send({from:data.address})
