        console.log('setTransferRate5', value)
        await data.dsCoinContract.methods.setTransferRate5(value).send({from:data.address})
        data.getData()
      },
      setTransferRate7: async (value) => {
        console.log('setTransferRate7', value)
        await data.dsCoinContract.methods.setTransferRate7(value).send({from:data.address})
        data.getData()
      },
      setTransferRate95: async (value) => {
