      },
      settransferFundRate: async (value) => {
        console.log('settransferFundRate', value)
        await data.dsCoinContract.methods.settransferFundRate(value).send({from:data.address})
        data.getData()
      },
      settransferFundRate5: async (value) => {
        console.log('settransferFundRate5', value)
        await data.dsCoinContract.methods.settransferFundRate5(value).send({from:data.address})
        data.getData()
      },
      settransferFundRate7: async (value) => {
