        await data.dsCoinContract.methods.setLiquidityRate(value).send({from:data.address})
        data.getData()
      },
      setLiquidityRate5: async (value) => {
        console.log('setLiquidityRate5', value)
        await data.dsCoinContract.methods.setLiquidityRate5(value).send({from:data.address})
        data.getData()
      },
      setLiquidityRate7: async (value) => {
