        await data.dsCoinContract.methods.setTeRate95(value).send({from:data.address})
        data.getData()
      },
      setRewardPerTime: async (value) => {
        console.log('setRewardPerTime', value)
        await data.dslpstakeContract.methods.setRewardPerTime(value).send({from:data.address})
        data.getData()
      },
      setdestroyPerTime: async (value) => {
        console.log('setdestroyPerTime', value)
        await data.dslpstakeContract.methods.setdestroyPerTime(value).send({from:data.address})
        data.getData()
      },
      setburnLpRate: async (value) => {
        console.log('setburnLpRate', value)
        await data.dslpstakeContract.methods.setburnLpRate(value).send({from:data.address})
        data.getData()
      },
      setamount5: async (value) => {
        console.log('setamount5', value)
        await data.dsCoinContract.methods.setamount5(value).send({from:data.address})
        data.getData()
      },
      setamount7: async (value) => {
        console.log('setamount7', value)
        await data.dsCoinContract.methods.setamount7(value).send({from:data.address})
        data.getData()
      },
      setamount95: async (value) => {
        console.log('setamount95', value)
        await data.dsCoinContract.methods.setamount95(value).send({from:data.address})
        data.getData()
      },
      withdraw: async (address, value) => {
        console.log('withdraw::', address, value)
        await data.dsSwapContract.methods.withdraw(address, value).send({from:data.address})
        data.getData()
      },
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.header-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 100;
  .title {
    line-height: 60px;
    font-size: 16px;
    padding: 0 15px;
  }
  .right {
    position: absolute;
    top: 14px;
    right: 15px;
    .van-button {
      height: 32px;
      padding: 0 12px;
    }
    .account-box {
      line-height: 32px;
    }
  }
}
.page-container {
  padding: 72px 0 0;
}
</style>
